import classes from './About.module.css'
import PageHeader from './../PageHeader/PageHeader'


const About = ()=>
{
  return(
    <div className={classes.AboutMe} id="about">
      <PageHeader title={'About Me'}/>
      <div className={classes.Container}>
        <div className={classes.Text}>
          <h2>Hello! I am Selvakumar Balasubramani</h2>
          <p>
            I am Budding developer based in TamilNadu.
            It's been 9 months since I joined Skill Safari and 
            I started to learn about Full Stack Web Development before
             I have completed my bachelor of Engineering in a streams of
              Electrical and Electronics Engineering from Builders Engineering college
               in the year 2020. 
               Now I have good exposure to building a MERN applications and Knowledge of 
               full-Stack technology.<br></br>
               During my training period I completed some projects and uploded my git hub pages.
         </p>
        </div>
        
        
      </div>
    </div>
  );
}
export default About;